nav.navbar {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
    -ms-flex-align: center;
        align-items: center;
    padding: 10px 10px;
    position: relative;
}

.menu {
    display: -ms-flexbox;
    display: flex;
}
.menu a {
    font-family: 'Exo 2';
    display: block;
    padding: 10px;
    text-transform: uppercase;
    /* font-family: 'Raleway', sans-serif; */
    font-weight: 600;
    font-stretch: expanded;
    position: relative;
    margin-left: 10px;
}

.menu a.active::after {
    content: '';
    position: absolute;
    bottom: 0;
    left: 0;
    border-bottom: 3px solid rgb(51, 167, 80);
    width: 100%;
    height: 1px;
}

.menu a:not(.active):hover::after {
    content: '';
    position: absolute;
    bottom: 0;
    left: 50%;
    -webkit-transform: translateX(-50%);
        -ms-transform: translateX(-50%);
            transform: translateX(-50%);
    border-bottom: 3px solid rgb(127, 223, 151);
    width: 100%;
    height: 1px;
    -webkit-animation: extend-underline 0.2s ease;
            animation: extend-underline 0.2s ease;
}
.expand-icon {
    display: none;
    font-size: 1.5rem;
    cursor: pointer;
    padding: 5px;
}

@-webkit-keyframes extend-underline {
    from {
        width: 0%;
    }
    to {
        width: 100%;
    }
}

@keyframes extend-underline {
    from {
        width: 0%;
    }
    to {
        width: 100%;
    }
}

@media screen and (max-width: 640px) {
    .no-scroll {
        overflow: hidden;
    }
    .menu {
        -ms-flex-direction: column;
            flex-direction: column;
        background-color: white;
        -webkit-transform-origin: top right;
            -ms-transform-origin: top right;
                transform-origin: top right;
        -webkit-animation: menu-appear 0.2s;
                animation: menu-appear 0.2s;        
        text-align: center;
    }
    .menu a {
        margin-left: 0px;
    }
    
    .menu a.active::after {
        display: none;
    }
    .menu a.active {
        background-color: rgb(51, 167, 80);
    }
    
    .menu a:not(.active):hover::after {
        display: none;
    }
    .menu a:not(.active):hover {
        background-color: rgb(127, 223, 151);
    }
    .menu-wrapper {
        height: 100vh;
        position: fixed;
        top: 90px;
        left: 0;
        width: 100%;
        z-index: 10;
        background-color: rgba(0,0,0,0.9);
        /* transition: all 0.3s ease-in; */
        -webkit-animation: menu-fade-in 0.2s;
                animation: menu-fade-in 0.2s;

    }
    .expand-icon {
        display: block;
    }
    .menu-hidden {
        display: none !important;
    }
}


@-webkit-keyframes menu-appear {
    from {
        -webkit-transform: scale(0.1);
                transform: scale(0.1);
        opacity: 0;
    }
    to {
        -webkit-transform: scale(1);
                transform: scale(1);
        opacity: 1;
    }
}


@keyframes menu-appear {
    from {
        -webkit-transform: scale(0.1);
                transform: scale(0.1);
        opacity: 0;
    }
    to {
        -webkit-transform: scale(1);
                transform: scale(1);
        opacity: 1;
    }
}
@-webkit-keyframes menu-fade-in {
    from {
        opacity: 0;
    }
    tp {
        opacity: 1;
    }
}
@keyframes menu-fade-in {
    from {
        opacity: 0;
    }
    tp {
        opacity: 1;
    }
}


