.codepens {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-align: center;
        align-items: center;
    padding-top: 20px;
}

.codepen-grid {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-pack: start;
        justify-content: flex-start;
    
    -ms-flex-wrap: wrap;
    
        flex-wrap: wrap;
    width: 100%;
}
.codepen-grid:after {
    content: "";
    -ms-flex-pack: start;
        justify-content: flex-start;
  }
/* .codepen-grid > * {
    flex-basis: 20%;
    transition: all 0.1s;
} */

.grid-item {
    height: 17.5vw;
    width: 17.5vw;
    margin: 1vw;
    -ms-flex-positive: 0;
        flex-grow: 0;
    -ms-flex-negative: 0;
        flex-shrink: 0;
}

.see-more {
    -ms-flex-item-align: center;
        align-self: center;
    margin: 30px 0;
    border-radius: 7px;
    border: 1px solid #327BB7;
    padding: 10px;
    -webkit-transition: 0.2s all;
    -o-transition: 0.2s all;
    transition: 0.2s all;
    font-family: 'Exo 2', sans-serif;
}
.see-more:active, .see-more:hover {
    background-color: #327BB7;
    color: white !important;
}

@media screen and (max-width: 1000px) {
    .grid-item {
        height: 36.5vw;
        width: 36.5vw;
    }
}

@media screen and (max-width: 500px) {
    .grid-item {
        height: 75vw;
        width: 75vw;
    }
}


