.project-card {
    display: -ms-flexbox;
    display: flex;
    margin-bottom: 70px;
    padding-bottom: 20px;
    -ms-flex-direction: column;
        flex-direction: column;
    /* border-bottom:1px rgb(233, 233, 233) solid; */
}

.project-card h3 {
    margin: 0;
    font-weight: 400;
    font-size: 1.6rem;
}
.project-description {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;    
    -ms-flex-align: stretch;    
        align-items: stretch;
    text-align: justify;
}

.thumbnail-wrapper {
    -ms-flex-negative: 0;
        flex-shrink: 0;
    -ms-flex-positive: 0;
        flex-grow: 0;
    margin-right: 20px;
    width: 250px;
    height: 250px;
}

.text-description {
    -ms-flex-preferred-size: 65%;
        flex-basis: 65%;
}

.text-description h3 {
    text-align: left;
}

.technology-stack {
    margin-top: 10px;    
    display: -ms-flexbox;    
    display: flex;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    -ms-flex-pack: center;
        justify-content: center;
}
.technology-stack img {
    height: 30px;
    margin-right: 3vw;
    margin-top: 10px;

}

.technology-stack img:last-child {
    margin-right: 0;
}



@media screen and (max-width: 736px) {
    .project-description {
        -ms-flex-direction: column;
            flex-direction: column;
    }
    .thumbnail-wrapper {
        margin-right: 0;
        margin-bottom: 15px;
        width: 100%;
    }
    .text-description h3 {
        text-align: center;
    }
}


