.email-form form {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
}

.contact-container {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-wrap: nowrap;
        flex-wrap: nowrap;
    -ms-flex-pack: justify;
        justify-content: space-between;
    width: 100%;
}

.contact-container h5 {
    font-size: 1.2rem;
    margin: 1rem 0;
    font-weight: 500;
}

.various-contacts {
    -ms-flex-preferred-size: 45%;
        flex-basis: 45%;
}
.various-contacts div {
    margin-bottom: 40px;
}
.email-form {
    -ms-flex-preferred-size: 50%;
        flex-basis: 50%;
}
.email-form form > * {
    margin-bottom: 10px;
    outline: none;
    border: none;
    background-color: rgb(235, 235, 235);
    padding: 5px;
}

.email-form form > *:focus:not(button) {
    background-color: white;
}

.email-form form > button {
    background-color: #4E87A2;
    color: white;
    text-transform: uppercase;
    cursor: pointer;
}

.web a {
    font-size: 2rem;
    margin-right: 10px;
}
.email-form textarea {
    resize:vertical;
}

.status-message {
    text-align: center;
    padding: 4px;
    font-family: sans-serif;
    font-size: 0.9rem;
}

.success {
    background-color: rgba(0,255,0, 0.2);
    /* border: 1px solid green; */
}
.error {
    background-color: rgba(255,0,0, 0.2);
    /* border: 1px solid brown; */
}

button.disabled {
    pointer-events: none;
    background-color: grey !important;
}

@media screen and (max-width: 640px) {
    .contact-container {
        -ms-flex-direction: column;
            flex-direction: column;
        -ms-flex-align: center;
            align-items: center;
    }
    .email-form, .various-contacts {
        width: 100%;
    }
}


