.Brand {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    cursor: pointer;
}
.Brand * {
    cursor: pointer;
}
.Brand img {
    height: 70px;
}
.Brand h1 {
    font-size: 1.5rem;
    margin: 0.5rem;
    color: #505050;
    font-family: 'Exo 2';
    text-transform: uppercase;
}


