.rounded-image {
    border-radius: 50%;
    height: 120px;
    width: 120px;
    background: url('./assets/avatar_e_1000x1000.jpg');
    background-position: center;
    background-size: cover;
}

.me {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    background: url('./assets/bg.jpg');
    background-size: cover;
    background-color: #4480A2;
    background-position: 10% 50%;
    -ms-flex-pack: justify;
        justify-content: space-between;
    /* box-shadow: 0px 2px 10px #257425; */
    -ms-flex-align: center;
        align-items: center;
    padding: 20px 0;
    color: rgb(230, 243, 243);
}
.me h1 {
    font-size: 3rem;
    margin: 1rem;
    /* font-family: 'Exo 2'; */
    /* color: #333333; */
    /* color: rgb(224, 245, 245); */
    
}
.me h3 {
    max-width: 400px;
    text-align: center;
    font-weight: normal;
    margin-top: 0;
    /* color: rgb(224, 245, 245); */
}
.me a {
    border-radius: 7px;
    display: block;
    /* background-color: #99CCCC; */
    background-color: rgb(193, 219, 219);
    padding: 2px 6px;
    /* height: 25px; */
    /* background-color: lightgreen; */
}
.me a:hover {
    background-color: rgb(224, 245, 245);
    /* border: 1px solid black; */
}


